.drop {
    display: flex;
    flex-direction: column;
    gap: var(--gap);
    &__item {
        width: 100%;
        aspect-ratio: 1 / 1;
        border-radius: var(--border-radius);
        background: var(--c-white);
        overflow: hidden;
        position: relative;
        &_grid {
            padding: calc(var(--gap) / 2);
            overflow-y: auto;
            .drag__item {
                display: inline-block;
                margin: calc(var(--gap) / 4);
                pointer-events: none;
            }
        }
        &_free {
            .drag__item {
                position: absolute;
            }
        }
        @include media-gt-xs {
            width: calc(100% / 2 - var(--gap) / 2);
            max-width: 500px;
        }
        .drag__item {
            border-color: transparent;
        }
    }
    @include media-gt-xs {
        flex-direction: row;
        justify-content: center;
    }
}
